body {
  font-family: Helvetica, Arial, sans-serif;
}

h1 {
  color: #fff;
  z-index: 10;
  margin: 0;
  font-size: 40pt;
  font-weight: 100;
  position: relative;
}

.glass {
  height: 100vh;
  width: 100vw;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}

.ingredient-options-list {
  z-index: 10;
  width: 400px;
  flex-flow: wrap;
  justify-content: space-between;
  list-style: none;
  display: flex;
  position: absolute;
  right: 50px;
}

.ingredient-button {
  width: 140px;
  color: #838ca2;
  height: 2em;
  cursor: pointer;
  z-index: 10;
  background-color: #15151a;
  border: 1px solid #838ca2;
  border-radius: 6px;
}

.ingredient-unit-button {
  width: 40px;
  color: #838ca2;
  height: 2em;
  cursor: pointer;
  z-index: 10;
  background-color: #15151a;
  border: 1px solid #838ca2;
  border-radius: 6px;
  margin-left: 5px;
}

.glass-actions {
  width: 100%;
  z-index: 10;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 20px;
}

.glass-actions button {
  color: #838ca2;
  height: 2em;
  cursor: pointer;
  background-color: #15151a;
  border: 2px solid #838ca2;
  border-radius: 6px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 40px;
}

#mix-button {
  min-width: 200px;
  flex-grow: 1;
  margin-right: 20px;
}

#dump-button {
  width: 2em;
}

.result {
  color: #fff;
  color: #838ca2;
  text-align: center;
  flex-grow: 1;
  font-size: 40px;
}

